import React from "react";

class Home extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            sqaure: 3,
            clickClass: { 1: "homeCard", 2: "homeCard" },
            clicked: {},
            cross: false,
            crossCombination: [],
            cicleCombination: [],
            won: false,
        };
    }

    componentWillMount() {
        for (let i = 1; i <= this.state.sqaure * this.state.sqaure; i++) {
            this.state.clickClass[i] = "homeCard";
        }
        //win combination 123 456 789 147 258 369 159 357
        // 1 - 2 3 4 7 5 9
    }

    checkWin = combination => {
        if (combination.indexOf("1") > -1 && combination.indexOf("2") > -1 && combination.indexOf("3") > -1)
            return true;
        if (combination.indexOf("4") > -1 && combination.indexOf("5") > -1 && combination.indexOf("6") > -1)
            return true;
        if (combination.indexOf("7") > -1 && combination.indexOf("8") > -1 && combination.indexOf("9") > -1)
            return true;
        if (combination.indexOf("1") > -1 && combination.indexOf("4") > -1 && combination.indexOf("7") > -1)
            return true;
        if (combination.indexOf("2") > -1 && combination.indexOf("5") > -1 && combination.indexOf("8") > -1)
            return true;
        if (combination.indexOf("3") > -1 && combination.indexOf("6") > -1 && combination.indexOf("9") > -1)
            return true;
        if (combination.indexOf("1") > -1 && combination.indexOf("5") > -1 && combination.indexOf("9") > -1)
            return true;
        if (combination.indexOf("3") > -1 && combination.indexOf("5") > -1 && combination.indexOf("7") > -1)
            return true;

        return false;
    };

    handleClick = e => {
        let element = e.target.id;
        let clickClass = this.state.clickClass;
        let clicked = this.state.clicked;
        let won = false;

        if (!clicked[element]) {
            clicked[element] = true;
            if (this.state.cross) clickClass[element] = "homeCard circle3 ";
            else clickClass[element] = "homeCard cross ";
        } else {
            return;
        }

        if (this.state.cross) {
            this.state.crossCombination.push(element);
            won = this.checkWin(this.state.crossCombination);
        } else {
            this.state.cicleCombination.push(element);
            won = this.checkWin(this.state.cicleCombination);
        }
        this.setState({
            clickClass: clickClass,
            clicked: clicked,
            cross: !this.state.cross,
            won: won,
        });
    };

    render() {
        return (
            <>
                <div className="homeMain">
                    <div className={this.state.won ? "gameOver" : "homeContent"} disabled={this.state.won}>
                        {Object.keys(this.state.clickClass).map(key => (
                            <div
                                key={key}
                                id={key}
                                disabled={this.state.clicked[key]}
                                className={this.state.clickClass[key]}
                                onClick={this.handleClick}
                            ></div>
                        ))}
                    </div>
                    {this.state.won && <div className="won">You Won</div>}
                </div>
            </>
        );
    }
}

export default Home;
