import React, { useEffect, useState, useRef } from "react";

import { Card, Button } from "react-bootstrap";

import { publish } from "./utils/pubSub";

function About(props) {
    const itemsRef = useRef([]);
    let [quantity, setQantity] = useState({});

    let items = [
        {
            id: 0,
            title: "Mouse",
            text: "Mouse",
            quantity: 0,
        },
        {
            id: 1,
            title: "Watch",
            text: "Watch",
            quantity: 0,
        },
        {
            id: 2,
            title: "Depam",
            text: "Depam",
            quantity: 0,
        },
        {
            id: 3,
            title: "Phone",
            text: "Phone",
            quantity: 0,
        },
        {
            id: 4,
            title: "Shampoo",
            text: "Shampoo",
            quantity: 0,
        },
    ];

    useEffect(() => {
        let quantity = {};

        for (let i of items) {
            quantity[i.id] = i.quantity;
        }

        setQantity(quantity);
    }, []);

    let handleAdd = id => event => {
        publish("addCart", 1);
        itemsRef.current[id].value = quantity[id] + 1;
        setQantity(prev => {
            prev[id] = quantity[id] + 1;
            console.log(prev);
            return prev;
        });
    };

    let handleInputChange = id => event => {
        publish("addCart", 1);

        setQantity(prev => {
            prev[id] = event.target.value;
            console.log(prev);
            return prev;
        });
    };

    return (
        <>
            <div style={{ display: "flex", flexWrap: "wrap" }}>
                {items.map((item, i) => {
                    return (
                        <Card key={item.id} style={{ width: "18rem", margin: "20px" }}>
                            <Card.Img
                                variant="top"
                                style={{ height: "100px", width: "80px" }}
                                src={process.env.PUBLIC_URL + "/" + item.id + ".jpg"}
                            />
                            <Card.Body>
                                <Card.Title>{item.title}</Card.Title>
                                <Card.Text>{item.text}</Card.Text>

                                <Button variant="primary" onClick={handleAdd(item.id)}>
                                    Add
                                </Button>
                                <input
                                    style={{ width: "40px", margin: "0px 20px" }}
                                    type="number"
                                    ref={el => (itemsRef.current[item.id] = el)}
                                    onChange={handleInputChange(item.id)}
                                ></input>
                            </Card.Body>
                        </Card>
                    );
                })}
            </div>
        </>
    );
}

export default About;
